@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/montserrat-regular.woff2") format("woff2");
  src: url("../fonts/montserrat-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/montserrat-medium.woff2") format("woff2");
  src: url("../fonts/montserrat-medium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/montserrat-semibold.woff2") format("woff2");
  src: url("../fonts/montserrat-semibold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/montserrat-bold.woff2") format("woff2");
  src: url("../fonts/montserrat-bold.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

.container {
  width: 320px;
  padding: 0 20px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 20px;
  }
}
@media (min-width: 1440px) {
  .container {
    width: 1440px;
    padding: 0 40px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  height: 100%;
}

.page__body {
  font-family: "Montserrat", "Arial", sans-serif;
  color: #333333;
  margin: 0;
  min-width: 320px;
  background-color: #ffffff;
}

.page__container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow-x: hidden;
}

.main-container {
  flex-grow: 1;
}

.main-header {
  min-height: 60px;
}
@media (min-width: 768px) {
  .main-header {
    min-height: 80px;
  }
}

.main-nav {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  position: relative;
}

.main-nav__toggle {
  position: absolute;
  top: 24px;
  right: 23px;
  width: 15px;
  height: 15px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.main-nav__list {
  list-style-type: none;
}
.main-nav__list--user-menu {
  flex-grow: 1;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  margin: 0 0 0 auto;
  max-width: 220px;
  padding: 0 40px 0 0;
}
.main-nav__list--site {
  order: 1;
  width: 320px;
  margin: 0;
  padding: 0;
}

.main-nav__link {
  display: block;
}
.main-nav__link--site {
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  color: #333333;
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 0.02em;
  padding: 21px 10px 20px;
}
.main-nav__link--site:active {
  background-color: #f0ebff;
}
.main-nav__link--user {
  padding: 30px;
  color: #333333;
}
.main-nav__link:active {
  background-color: #f0ebff;
}

.main-nav__item {
  position: relative;
}
.main-nav__item--site {
  text-align: center;
}

.main-nav__description {
  display: none;
}

.main-nav__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.main-nav__icon-description {
  display: none;
}

.main-nav__logo {
  align-self: flex-start;
  margin-top: 12px;
}

@media (max-width: 767.98px) {
  .main-nav__item--site:first-child::before {
    content: "";
    position: absolute;
    width: 100vw;
    height: 1px;
    background-color: #f0ebff;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
  }
  .main-nav__item--site::after {
    content: "";
    position: absolute;
    width: 100vw;
    height: 1px;
    background-color: #f0ebff;
    left: 50%;
    transform: translateX(-50%);
    bottom: 0;
  }

  .main-nav--closed .main-nav__toggle::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 16px;
    height: 2px;
    background-color: #231f20;
    box-shadow: 0 5px 0 0 #333333, 0 10px 0 0 #333333;
  }

  .main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
    content: "";
    position: absolute;
    top: 5px;
    left: 0;
    width: 15px;
    height: 2px;
    background-color: #333333;
  }
  .main-nav--opened .main-nav__toggle::before {
    transform: rotate(45deg);
    box-shadow: none;
  }
  .main-nav--opened .main-nav__toggle::after {
    transform: rotate(-45deg);
  }

  .main-nav--closed .main-nav__list--site {
    display: none;
  }

  .main-nav--opened .main-nav__list--site {
    position: absolute;
    top: 60px;
    background-color: #ffffff;
    width: 100vw;
    left: 50%;
    transform: translateX(-50%);
    z-index: 11;
  }

  .main-nav--nojs .main-nav__list--site {
    position: static;
    display: block;
  }

  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }

  .main-nav__logo {
    margin-top: 22px;
  }

  .main-nav__logo-image {
    width: 82px;
    height: 36px;
  }

  .main-nav {
    display: grid;
    grid-template-columns: 149px 1fr 181px;
    max-width: 768px;
    align-items: start;
  }

  .main-nav__item--site {
    padding: 0;
    max-width: 115px;
  }

  .main-nav__link {
    text-decoration: none;
  }
  .main-nav__link--site {
    display: block;
    padding: 31px 20px 30px;
  }
  .main-nav__link--site:hover {
    color: #7859cf;
  }
  .main-nav__link--site:focus {
    color: #7859cf;
    outline: none;
  }
  .main-nav__link--site:not(.main-nav__link--current):active {
    color: #7859cf;
    background-color: #f0ebff;
  }
  .main-nav__link--current {
    border-bottom: 2px solid #7859cf;
    color: #7859cf;
  }
  .main-nav__link--user {
    padding: 40px 30px;
  }

  .main-nav__list--site {
    order: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: auto;
  }
  .main-nav__list--user-menu {
    margin: 0;
    padding: 0;
    max-width: 240px;
  }
}
@media (min-width: 1440px) {
  .main-nav {
    max-width: 1440px;
    grid-template-columns: 170px 170px 1fr 400px;
    column-gap: 20px;
  }

  .main-nav__logo-image {
    width: 170px;
    height: 36px;
  }

  .main-nav__description {
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    display: block;
    align-self: flex-start;
    margin-top: 24px;
  }

  .main-nav__list--site {
    padding: 0 4px 0 100px;
  }
  .main-nav__list--user-menu {
    justify-content: flex-end;
    max-width: 390px;
    padding: 0;
  }

  .main-nav__icon-description {
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
    display: inline-block;
    text-transform: uppercase;
  }

  .main-nav__item--user {
    width: 130px;
    text-align: end;
  }

  .main-nav__icon {
    left: 38px;
  }

  .main-nav__link--user {
    padding: 29px 5px 31px 54px;
    letter-spacing: 0.02em;
    text-align: left;
  }
  .main-nav__link--user:hover {
    color: #7859cf;
  }
  .main-nav__link--user:hover .main-nav__icon {
    fill: #7859cf;
  }
  .main-nav__link--user:focus {
    color: #7859cf;
    outline: none;
  }
  .main-nav__link--user:focus .main-nav__icon {
    fill: #7859cf;
  }
  .main-nav__link--user:active {
    color: #7859cf;
    background-color: #f0ebff;
  }
  .main-nav__link--user:active .main-nav__icon {
    fill: #7859cf;
  }
}
.promo {
  width: 100%;
  height: 100%;
}

.promo__list {
  list-style-type: none;
  padding: 0;
  height: 100%;
  margin: 0;
}

.promo__item--first {
  background-color: #f3ebe1;
}
.promo__item--second {
  background-color: #eae6fc;
}
.promo__item--third {
  background-color: #e5e6e8;
}

.promo__slide-content {
  position: relative;
  padding: 310px 0 54px;
  text-align: center;
  min-height: 656px;
}
.promo__slide-content::before {
  content: "";
  position: absolute;
  width: 280px;
  height: 280px;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: contain;
}
.promo__slide-content--first::before {
  background-image: url("../img/flat-white-slider.png");
}
@media (min-resolution: 2dppx) {
  .promo__slide-content--first::before {
    background-image: url("../img/flat-white-slider@2x.png");
  }
}
.promo__slide-content--second::before {
  background-image: url("../img/lavender-slider.png");
}
@media (min-resolution: 2dppx) {
  .promo__slide-content--second::before {
    background-image: url("../img/lavender-slider@2x.png");
  }
}
.promo__slide-content--third::before {
  background-image: url("../img/espresso-slider.png");
}
@media (min-resolution: 2dppx) {
  .promo__slide-content--third::before {
    background-image: url("../img/espresso-slider@2x.png");
  }
}

.promo__flag {
  font-weight: 700;
  font-size: 12px;
  line-height: 16px;
  color: #7859cf;
  text-transform: uppercase;
  max-width: 280px;
  display: block;
  margin: 0 auto;
  letter-spacing: 0.04em;
}

.promo__title {
  font-weight: 700;
  font-size: 36px;
  line-height: 48px;
  color: #333333;
  letter-spacing: 0.02em;
  max-width: 280px;
  display: block;
  margin: 5px auto 10px;
}

.promo__description {
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #333333;
  max-width: 280px;
  display: block;
  margin: 10px auto 19px;
}

.promo__order-wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 230px;
  margin: 0 auto;
}

.promo__price-wrap {
  padding-right: 21px;
  padding-top: 4px;
}

.promo__price--old {
  font-weight: 400;
  font-size: 16px;
  line-height: 32px;
  color: #7859cf;
  text-decoration: line-through;
}
.promo__price--new {
  font-weight: 600;
  font-size: 20px;
  line-height: 32px;
  color: #333333;
}

.promo__button {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #ffffff;
  text-decoration: none;
  border-radius: 4px;
  background-color: #7859cf;
  width: 113px;
  padding: 8px 20px 9px;
  margin: 0 auto 5px;
}
.promo__button:hover {
  background-color: #9070ec;
}
.promo__button:focus {
  background-color: #9070ec;
  outline: none;
}
.promo__button:active {
  background-color: #593cac;
}
.promo__button:disabled {
  background-color: #bdbdbd;
}

.promo__slider-button {
  top: 10px;
  color: #7859cf;
  height: 100%;
  width: 20px;
}
.promo__slider-button:focus-visible {
  outline: none;
}
.promo__slider-button--prev {
  left: 0;
}
.promo__slider-button--prev:hover {
  background: linear-gradient(90deg, #ffffff 0%, rgba(255, 255, 255, 0) 100%);
}
.promo__slider-button--prev:focus {
  background: linear-gradient(90deg, #ffffff 0%, rgba(255, 255, 255, 0) 100%);
}
.promo__slider-button--prev:active {
  background: linear-gradient(90deg, rgba(120, 89, 207, 0.25) 0%, rgba(120, 89, 207, 0) 100%);
}
.promo__slider-button--next {
  right: 0;
}
.promo__slider-button--next:hover {
  background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, #ffffff 100%);
}
.promo__slider-button--next:focus {
  background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, #ffffff 100%);
}
.promo__slider-button--next:active {
  background: linear-gradient(90deg, rgba(120, 89, 207, 0) 0%, rgba(120, 89, 207, 0.25) 100%);
}

.promo__pagination {
  display: none;
}

