.main-footer__item:not(:last-child) {
  margin-bottom: 20px;
}

.main-footer__contacts {
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #333333;
  position: relative;
  padding: 6px 0 0 19px;
}
.main-footer__contacts::before {
  content: "";
  position: absolute;
  width: 13px;
  height: 16px;
  left: 0;
  top: 7px;
}
.main-footer__contacts--address {
  margin: 0 0 15px;
}
.main-footer__contacts--address::before {
  background: url("../img/address.svg") no-repeat center;
}
.main-footer__contacts--phone {
  text-decoration: none;
}
.main-footer__contacts--phone::before {
  background: url("../img/phone.svg") no-repeat center;
}

.main-footer__navigation-block {
  position: relative;
  padding: 5px 0 16px;
}
.main-footer__navigation-block::after {
  content: "";
  width: 100vw;
  height: 1px;
  background-color: #f0ebff;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}
.main-footer__navigation-block--contact {
  padding-bottom: 21px;
}

.main-footer__copyright-wrap {
  display: flex;
  justify-content: center;
  align-items: baseline;
  padding: 20px 20px 30px;
  max-width: 280px;
}

.main-footer__copyright-text {
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  color: #bdbdbd;
  margin: 0;
  padding-right: 4px;
}

.main-footer__copyright-link {
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  color: #bdbdbd;
  text-decoration: none;
}
.main-footer__copyright-link:hover {
  color: #7859cf;
}
.main-footer__copyright-link:hover .main-footer__copyright-icon {
  fill: #7859cf;
}
.main-footer__copyright-link:focus {
  color: #7859cf;
}
.main-footer__copyright-link:focus .main-footer__copyright-icon {
  fill: #7859cf;
}
.main-footer__copyright-link:active {
  color: #593cac;
}
.main-footer__copyright-link:active .main-footer__copyright-icon {
  fill: #593cac;
}

.main-footer__copyright-icon {
  position: relative;
  top: 2px;
}

@media (min-width: 768px) {
  .main-footer__navigation-group {
    display: grid;
    grid-template-columns: repeat(3, 223px);
    column-gap: 30px;
  }

  .main-footer__navigation-block {
    padding: 0;
  }
  .main-footer__navigation-block::after {
    display: none;
  }

  .main-footer__copyright-wrap {
    max-width: 700px;
    justify-content: flex-start;
    margin-left: 241px;
  }
}
@media (min-width: 1440px) {
  .main-footer {
    display: flex;
    flex-wrap: wrap;
    padding: 35px 40px 0;
  }

  .main-footer__copyright-wrap {
    width: 100%;
    max-width: 1360px;
    margin: 0;
    justify-content: center;
    padding-top: 11px;
  }

  .main-footer__navigation-group {
    grid-template-columns: repeat(3, 317px);
  }
}
